<template>
  <section>
    <!-- <button @click="openModal">AddBook</button> -->
    <!-- <Modal :isModalShown="isModalShown" @closemodal="closemodal">-->
      <AddBook />
    <!-- </Modal> --> 
    <button class="mobile-filter-btn primary-btn" @click="openFilter">
      {{ isFilterOpen ? "Close" : " Filter Books" }}
    </button>
    <book-filter
      @filtered="setFilter"
      :isFilterOpen="isFilterOpen"
    ></book-filter>
    <book-list :books="booksToShow"></book-list>
  </section>
</template>

<script>
import Modal from '../components/Modal.vue'
import AddBook from "../components/AddBook.vue";

import BookFilter from "../components/BookFilter";
import BookList from "../components/BookList";
import { bookService } from "../services/bookService.js";
export default {
  data() {
    return {
      filterBy: {
        title: "",
        minPrice: -Infinity,
        maxPrice: Infinity,
      },
      isFilterOpen: false,
      isModalShown: false,
    };
  },
  computed: {
    booksToShow() {
      const books = this.$store.getters.booksToShow;
      return books.filter((book) => {
        return (
          book.title
            .toLowerCase()
            .includes(this.filterBy.title.toLowerCase()) &&
          book.listPrice.amount < this.filterBy.maxPrice &&
          book.listPrice.amount > this.filterBy.minPrice
        );
      });
    },
  },
  async created() {
    this.loadBooks();
  },
  methods: {
    openModal() {
      this.isModalShown = true;
    },
    closemodal() {
      this.isModalShown = false;
    },
    async loadBooks() {
      this.$store.dispatch({ type: "loadBooks" });
    },
    setFilter(filter) {
      if (!filter.maxPrice) filter.maxPrice = Infinity;
      if (!filter.minPrice) filter.minPrice = -Infinity;
      this.filterBy = filter;
    },
    openFilter() {
      this.isFilterOpen = !this.isFilterOpen;
    },
  },

  components: {
    BookList,
    BookFilter,
    AddBook, Modal
  },
};
</script>

