<template>
  <div
    v-if="isModalShown"
    class="modal-screen flex align-center justify-center"
    @keydown="checkKey"
    @mousedown="closeModal"
  >
    <div class="modal-container" @mousedown.stop="">
      <div class="modal-content">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["isModalShown"],
  methods: {
    checkKey() {
      if (ev.key === "Escape") {
        this.closeModal();
      }
    },
    closeModal() {
      this.$emit("closemodal");
    },
  },
};
</script>

