<template>
  <section class="add-book">
    <input
      type="text"
      v-model="bookSearch"
      class="styled-input"
      @input="findBook"
      placeholder="Add book by name"
    />
    <div class="search-book-container">
      <ul class="google-books-list" v-if="bookSearch.trim().length > 3">
        <li
          class="google-book-title"
          v-for="(res, index) in searchRes"
          :key="index"
          @click="onAddBook($event, res)"
        >
          {{ res.volumeInfo.title }}
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
import { bookService } from "../services/bookService";
export default {
  data() {
    return {
      bookSearch: "",
      searchRes: [],
    };
  },
  methods: {
    async findBook(ev) {
      if (ev.target.value.trim().length > 2) {
        this.searchRes = await bookService.getBooksApi(ev.target.value);
      } else this.searchRes = [];
    },
    onAddBook(ev, book) {
      this.$store.dispatch({ type: "addBook", book });
      this.searchRes = [];
      this.bookSearch= ""
    },
  },
};
</script>

