<template>
  <div v-if="book">
    <div class="book-details main-container">
      <div class="flex column" key="{book.id}">
        <div class="flex details-wrap">
          <div class="details-part1">
            <h2>{{ book.title }}</h2>
            <h4>
              By (Author) {{book.authors.join(', ') || 'Unknown'}}
            </h4>
            <p>{{ book.subtitle }}</p>
            <h4>Description:</h4>
            <LongText :txt="book.description" />
          </div>
          <BookImg :book="book" />
        </div>
      </div>
    </div>
    <div class="product-details-section main-container full">
      <h2>Product Details</h2>
      <div class="product-details flex column">
        <p
          :class="[
            'bold',
            {
              red: book.listPrice.amount > 150,
              green: book.listPrice.amount > 0 && book.listPrice.amount < 20,
            },
          ]"
        >
          Price:
          <span>
            {{ getCurrency
            }}{{
              book.listPrice.amount !== 0
                ? book.listPrice.amount.toFixed(2)
                : "Not for sale"
            }}</span
          >
        </p>
        <p>
          Publication Date:
          <span> {{ book.publishedDate || 'Unknown' }} {{ labelPublishDate }} </span>
        </p>
        <p>
          Categories:<span> {{ book.categories.toString() }} </span>
        </p>
        <p>
          Language: <span>{{ getFullLanguage }} </span>
        </p>
        <p>
          Pages:
          <span>{{ book.pageCount || 'Unknown'  }} - {{ pagesMark }} </span>
        </p>
        <p>
          Product ID: <span>{{ book.id }} </span>
        </p>
        <RatingAvg :reviews="book.reviews" />
      </div>
    </div>
    <button class="primary-btn" @click="openAddReview">
      {{ isAddReview ? "close" : "Add review" }}
    </button>
    <Reviews
      :bookId="book.id"
      :reviews="book.reviews"
      :isAddReview="isAddReview"
      @closeAdd="closeAdd"
    />
    <button class="primary-btn" @click="backToHome">Back</button>
  </div>
</template>

<script>
import Reviews from "../components/Reviews.vue";
import LongText from "@/components/LongText";
import BookImg from "@/components/BookImg";
import { bookService } from "@/services/bookService.js";
import { utilsService } from "@/services/utilsService.js";
import RatingAvg from "../components/RatingAvg.vue";
export default {
  data() {
    return {
      book: null,
      isAddReview: false,
    };
  },
  async created() {
    const { id } = this.$route.params;
    this.book = await bookService.getById(id);
  },
  methods: {
    backToHome() {
      this.$router.push("/book");
    },
    openAddReview() {
      this.isAddReview = !this.isAddReview;
    },
    closeAdd() {
      this.isAddReview = false;
    },
  },

  computed: {
    pagesMark() {
      let pages = this.book.pageCount;
      if (pages > 500) return "Long reading";
      else if (pages > 200) return "Decent Reading";
      else if (pages < 100) return "Light Reading";
    },
    labelPublishDate() {
      let year = this.book.publishedDate;
      let currYear = new Date().getFullYear();
      let diff = currYear - year;
      if (diff < 1) return "(New!)";
      else if (diff > 10) return "(Veteran Book)";
    },
    getCurrency() {
      return utilsService.findCurrency(this.book.listPrice.currencyCode);
    },

    getFullLanguage() {
      let lang = this.book.language;
      let currLang;
      switch (lang) {
        case "en":
          currLang = "English";
          break;
        case "he":
          currLang = "Hebrew";
          break;
        case "sp":
          currLang = "Spanish";
          break;
      }
      return currLang;
    },
  },
  components: {
    BookImg,
    RatingAvg,
    LongText,
    Reviews,
  },
};
</script>





    